<template>
  <div>
    <section class="teamView__counter">
      <div
        class="teamView__counter__ally"
        v-for="(value, key) in Object.keys(results.team)"
        :class="{'teamView__counter__ally--highlight':value === selected}"
        :key="key"
        @click="changeCalcTarget(value)"
      >
        <img :src="'http://bulbadot.herokuapp.com/2d/'+value+'.png'">
        <p>{{value}}</p>
      </div>
    </section>

    <section class="teamView__battle">
      <section v-show="selected">
        <div
          class="teamView__battle__members"
          v-for="(match, key) in Object.keys(results.team[selected]).slice((start*multiplier)-10, (end*multiplier))"
          :key="key"
        >
          <div
            @click="openView(results.team[selected][match].targetedBy)"
            class="center teamView__battle__members--img"
          >
            <div><img :src="'http://bulbadot.herokuapp.com/2d/'+results.team[selected][match].targetedBy+'.png'"></div>
          </div>
          <div class="center teamView__battle__members--evs">
            {{results.team[selected][match].evs}}
          </div>
          <div class="center teamView__battle__members--move">
            {{results.team[selected][match].move}}
          </div>
          <div class="center teamView__battle__members--damage">
            {{results.team[selected][match].damage.toFixed(2)}}
          </div>
        </div>

        <div class="dexview__slider dexview__slider--med">
          <a class="dexview__slider__back" @click="go(-1)"></a>
          <div class="dexview__slider__box"><p>{{end*multiplier}} / {{size}}</p></div>
          <a class="dexview__slider__go" @click="go(1)"></a>
        </div>
      </section>
    </section>
  </div>
</template>

<script>
import minMaxStat from "@/services/minMaxStat.js";
import doBattle from "@/services/doBattle.js";

export default {
  props: ["data"],
  data() {
    return {
      results: false,
      selected: false,
      start: 10,
      end: 10,
      multiplier: 1,
      size: 0
    };
  },
  methods: {
    openView(pkmn) {
      const tier = this.data.weak[pkmn].tier;
      const routeData = this.$router.resolve({
        name: "pokemon",
        params: { tier: tier, pokemon: pkmn }
      });
      window.open(routeData.href, "_blank");
    },
    changeCalcTarget(pkmn){
      this.selected = pkmn
    },
    go(value) {
      console.log(this.size)
      value = this.multiplier + value;
      if (value <= 0) {
        return;
      }
      if (value > Math.ceil(this.size / 10)) {
        return;
      }
      console.log(this.multiplier)
      this.multiplier = value;
    }
  },
  created() {
    //let enemyEVs = {};
    let battle = { team: {}, aggr: {} };
    for (const key in this.data.weak) {
      if (this.data.weak[key]) {
        const enemy = this.data.weak[key];
        enemy.parsedSpreads = minMaxStat(enemy.spreads);

        for (const index in enemy.moves) {
          const move = enemy.moves[index];

          if (Number(move.power) > 0) {
            for (let p in this.data.pokemon) {
              const foe = this.data.pokemon[p];
              const r = doBattle(move, foe, enemy);

              if (r.aggressor in battle.aggr) {
                battle.aggr[r.aggressor].push({
                  foe: r.victim,
                  damage: r.damage,
                  move: r.move
                });
              } else {
                battle.aggr[r.aggressor] = [];
                battle.aggr[r.aggressor].push({
                  foe: r.victim,
                  damage: r.damage,
                  move: r.move
                });
              }

              if (r.victim in battle.team) {
                battle.team[r.victim].push({
                  targetedBy: r.aggressor,
                  damage: r.damage,
                  move: r.move,
                  evs: r.evs
                });
              } else {
                battle.team[r.victim] = [];
                battle.team[r.victim].push({
                  targetedBy: r.aggressor,
                  damage: r.damage,
                  move: r.move,
                  evs: r.evs
                });
              }
            }
          }
        }
      }
    }

    for (const pkmn in battle.team) {
      battle.team[pkmn].sort(function(a, b) {
        return b.damage - a.damage;
      });
    }
    this.results = battle;
    this.selected = Object.keys(this.results.team)[0];
    this.size = Object.keys(this.results.team[this.selected]).length
  }
};
</script>